.menu {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px; // 32 px pixelperfect
  color: #000000;
  z-index: 2;


}

@media (max-width: $tablet-width) {
  .menu--closed .menu__item {
    display: none;
  }

  .menu--closed .menu__item--active {
    display: block;
  }
}

.menu__list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  margin: 0;
  padding: 0;
  list-style: none;
}

.menu__item {
  position: relative;
  box-sizing: border-box;
  padding-left: 76px;

  width: 100%;
  height: 79px;
  box-shadow: 0px 1px 0px #ECECEC, inset 0px 1px 0px #ECECEC;
}

.menu__item--logo {
  padding: 0;

  &:hover {
    opacity: 0.6;
  }

}

.menu__image {
  margin-top: 7px;
}

/* .menu__item--search {
  background-image: url("../../img/search.svg");
  background-repeat: no-repeat;
  background-position: left 32px center;
} */

/* .menu__item--cart {
  background-image: url("../../img/cart.svg");
  background-repeat: no-repeat;
  background-position: left 32px center;
} */

.menu__item--tablet {
  display: none;
}

.menu__item--desktop {
  display: none;
}

.menu__link {

  display: flex;
  justify-content: flex-start;
  align-items: center;

  width: 100%;
  height: 100%;
  text-decoration: none;
  color: inherit;

  &:hover .menu__text{
    opacity: 0.6;
  }

  &:hover .menu__icon{
    color: #63D1BB;
  }

  &:active {
    opacity: 0.3;
  }
}

.menu__icon {
  position: absolute;
  left: 32px;
  top: 32px;

  color: rgba(0, 0, 0, 0.2);
}

.menu__link--logo {
  justify-content: center;
}

@media (min-width: $tablet-width) {
  .menu__list {
    display: grid;
    grid-template-columns: 218px 153px 82px 315px;
    grid-template-rows: 88px 67px;
  }

  .menu__item {
    width: auto;
    height: 100%;
    max-height: 100%;
    padding: 0;

    box-shadow: none;
  }

  .menu__item--logo {
    order: 1;
    grid-column: 1/3;
    padding-left: 50px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    border-right: 1px solid rgba(0, 0, 0, 0.1);

    & .menu__link {
      justify-content: flex-start;
    }

    & .menu__image {
      margin-top: 10px;
      width: 113px;
      height: auto;
    }
  }

  .menu__item--search {
    order: 2;
    grid-column: 3/4;

    border-bottom: 1px solid rgba(0, 0, 0, 0.1);

    & .menu__link {
      font-size: 0;
    }
  }

  .menu__item--cart {
    order: 3;
    grid-column: 4/6;
    padding-right: 50px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    border-left: 1px solid rgba(0, 0, 0, 0.1);

    & .menu__link {
      justify-content: flex-end;
    }
  }

  .menu__item--catalog {
    order: 4;
    padding-left: 50px;
    background-color: transparent;

    font-size: 15px;
    line-height: 24px;
  }

  .menu__item--form {
    order: 5;

    background-color: transparent;

    font-size: 15px;
    line-height: 24px;
  }

  .menu__item--delivery {
    order: 6;
    grid-column: -1/-3;
    padding-right: 50px;
    display: block;
    background-color: transparent;

    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: #444444;

    & .menu__link {
      justify-content: flex-end;
    }
  }

  .menu__item--tablet {
    display: block;
  }
}

@media (min-width: $desktop-width) {
  .menu__list {
    grid-template-columns: 234px 165px 175px 175px 83px 317px;
  }

  .menu__item {
    font-weight: 700;
    font-size: 15px;
    line-height: 24px;
  }

  .menu__item--desktop {
    display: block;
  }

  .menu__item--catalog {
    order: 1;

    font-weight: 700;
    font-size: 17px;
    line-height: 24px;

    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    border-right: 1px solid rgba(0, 0, 0, 0.1);

  }

  .menu__item--form {
    order: 2;

    padding-left: 35px;
    white-space: nowrap;

    font-weight: 700;
    font-size: 17px;
    line-height: 24px;

    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }

  .menu__item--logo {
    order: 3;

    grid-column: 3/5;
    padding: 0;



    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    border-right: 1px solid rgba(0, 0, 0, 0.1);

    & .menu__link {
      justify-content: center;
    }

    & .menu__image {
      margin-top: 10px;
      width: 150px;
      height: auto;
    }
  }

  .menu__item--search {
    order: 4;
    grid-column: 5/6;

    font-weight: 700;
    font-size: 17px;
    line-height: 24px;
  }

  .menu__item--cart {
    order: 5;
    grid-column: 6/7;

    font-weight: 700;
    font-size: 17px;
    line-height: 24px;
  }

  .menu__item--new {
    order: 6;
    padding-left: 50px;
  }

  .menu__item--sale {
    order: 7;

    & .menu__link {
      justify-content: center;
    }
  }

  .menu__item--delivery {
    order: 8;

    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
  }
}


